<template>
  <loading :active="isLoading"></loading>
  <pagesBanner></pagesBanner>
  <section class="completed pages position-relative overflow-hidden">
    <div class="container position-relative z-index-1 box_">
      <div class="bread-crumbs py-3 px-lg-5">
        <ol
          itemscope=""
          itemtype="http://schema.org/breadcrumbList"
          id="breadcrumbs"
        >
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <router-link to="/" itemprop="item" title="劉記花生">
              <span itemprop="name">首頁</span>
            </router-link>
            <meta itemprop="position" content="1" />
          </li>
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <span class="mx-2">/</span>
            <router-link :to="`/completed/${order.id}`" itemprop="item" :title="order.is_paid ? '訂購完成' : '模擬付款'">
              <span itemprop="name" v-if="order.is_paid">訂購完成</span>
              <span itemprop="name" v-else>模擬付款</span>
            </router-link>
            <meta itemprop="position" content="2" />
          </li>
        </ol>
      </div>
      <div class="main-box bg-white px-lg-5 px-4 py-lg-5 py-4 mb-4">
        <div class="row justify-content-center">
          <div class="col-lg-8 p-0 cart3">
            <div class="text-center mb-4">
              <h2 class="d-flex align-items-center justify-content-center">
                <template v-if="order.is_paid">
                  <span class="me-2"><i class="bi bi-check-circle text-danger"></i></span>訂購完成
                </template>
                <template v-else>
                  <span class="me-2"><i class="bi bi-cash-coin"></i></span>模擬付款
                </template>
              </h2>
              <div v-if="order.is_paid">謝謝您，您的訂單已經成立！</div>
              <div v-else>模擬付款中！</div>
            </div>
            <div class="bc_box_c p-4 mb-4">
              <div class="title mb-2">訂單資訊</div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">訂單編號：</div>
                <div class="col">{{order.id}}</div>
              </div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">訂單日期：</div>
                <div class="col">{{ $filters.date(order.create_at)}}</div>
              </div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">訂單狀態：</div>
                <div class="col">處理中</div>
              </div>
            </div>
            <div class="bc_box_c p-4 mb-4">
              <div class="title mb-2">商品資訊</div>
              <table class="table align-middle">
                <thead>
                <th>品名</th>
                <th>數量</th>
                <th class="text-end">小計</th>
                </thead>
                <tbody>
                <tr v-for="item in order.products" :key="item.id">
                  <td>{{ item.product.title }}</td>
                  <td>{{ item.qty }}/{{ item.product.unit }}</td>
                  <td class="text-end">{{ $filters.currency(item.final_total) }}</td>
                </tr>
                </tbody>
                <tfoot>
                <tr>
                  <td colspan="2" class="text-end">總計</td>
                  <td class="text-end">{{ $filters.currency(order.total) }}</td>
                </tr>
                </tfoot>
              </table>
            </div>
            <div class="bc_box_c p-4 mb-4">
              <div class="title mb-2">付款資訊</div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">付款方式：</div>
                <div class="col">信用卡付款</div>
              </div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">付款狀態：</div>
                <div class="col"> <span v-if="order.is_paid">已付款</span><span v-else>未付款</span> </div>
              </div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">發票號碼：</div>
                <div class="col">BJ-11111111</div>
              </div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">發票狀態：</div>
                <div class="col">已開立</div>
              </div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">發票類型：</div>
                <div class="col">二聯式</div>
              </div>
            </div>
            <div class="bc_box_c p-4 mb-4">
              <div class="title mb-2">收件資訊</div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">收件人：</div>
                <div class="col">{{order.user.name}}</div>
              </div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">收件人電話：</div>
                <div class="col">{{order.user.tel}}</div>
              </div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">配送方式：</div>
                <div class="col">宅配</div>
              </div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">配送地址：</div>
                <div class="col">{{order.user.address}}</div>
              </div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">配送狀態：</div>
                <div class="col">備貨中</div>
              </div>
              <div class="row mb-1 mx-0">
                <div class="item_col p-0">配送說明：</div>
                <div class="col">
                  <strong class="d-block mb-1">「當日配送注意事項」</strong>
                  <ul class="pl-4">
                    <li>配送地區：限台北市區</li>
                    <li>單筆訂單滿NT$ 2,500以上，運費NT$ 380</li>
                    <li>單筆訂單滿 NT$ 5,000 以上，免運</li>
                  </ul>
                </div>
              </div>
            </div>
            <div class="text-center btn-box">
              <router-link to="/" class="btn btn-primary" v-if="order.is_paid">回首頁</router-link>
              <div class="btn btn-primary" v-else @click.prevent="goPay">進行付款</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import pagesBanner from '../components/pagesBanner.vue'
export default {
  components: {
    pagesBanner
  },
  data () {
    return {
      isLoading: false,
      orderId: '',
      order: {
        user: {}
      }
    }
  },
  methods: {
    simpleScrollTrigger () {
      this.gsap.from('.box_', {
        y: 50,
        opacity: 0,
        duration: 1,
        delay: 0.5,
        scrollTrigger: {
          trigger: '.box_',
          start: 'top 90%'
        }
      })
    },
    getData () {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/order/${this.orderId}`
      this.$http.get(api).then((res) => {
        this.isLoading = false
        if (res.data.success) {
          this.order = res.data.order
        }
      })
    },
    goPay () {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/pay/${this.orderId}`
      this.$http.post(api).then((res) => {
        this.isLoading = false
        if (res.data.success) {
          document.documentElement.scrollTop = 0
          this.getData()
        }
      })
    }
  },
  created () {
    this.orderId = this.$route.params.orderId
  },
  mounted () {
    this.getData()
    this.simpleScrollTrigger()
  }
}
</script>
